.SearchBar{
    display: -ms-flexbox;
    display: flex;
}
.Bar{
    margin-top:1em;
    background-color: #703B09;
    width: 100%;
    height:3em;
    border-radius: 2em;
}
ul{
    list-style-type: none;
    margin: 10px;
    padding: 0;
}
h1{
    color:white;
}
.searchImage{
    display:-ms-flexbox;
    display:flex;
    -ms-flex-pack: center;
        justify-content: center;
}
.searchImage > img{
    height:150px;
    width: 150px;
    border-radius: 20px;
    margin: 5px;
}



